@import "../../config.scss";

ul {
  margin: 0;
  padding: 0;
}

.list {
  margin: 0;
  padding: 0;
  //display: table-row;

  li {
    padding: 0;
    display: table-cell;
    list-style-type: none;
    margin: 10px;
    vertical-align: middle;


    a, span, button {
      display: table-cell;
      vertical-align: middle;
      color: $secondaryTextColor;
      text-decoration: none;
      background: transparent;
      border: none;
      box-shadow: none;
      margin-right: 0;
    }

    button {
      cursor: pointer;
    }

    a:hover, button:hover {
      color: $primaryColor;
    }

    .active {
      color: $primaryColor;
    }

    .no-bg {
      background: transparent !important;
    }
  }
}

.default-list {
  li {
    height: 33px;

    a, span, button {
      padding: 0 7px 0 7px;
      height: 31px;
    }
  }
}

.aside-list {
  width: 22px;
  float: left;
  height: 100%;
  border-right: 1px solid $borderColor;

  li {
    transform: rotate(90deg);
    transform-origin: right top;
    -ms-transform: rotate(90deg);
    -ms-transform-origin: right top;
    -webkit-transform: rotate(90deg);
    -webkit-transform-origin: right top;
    display: block;
    font-weight: bold;
    //margin-top:  20px;
    margin: 0;
    //margin-bottom: 100px;
    left: -2px;
    position: relative;
    border-left: 1px solid $borderColor;
    padding-left: 15px;

    li:first-of-type {
      border-left: none !important;
    }

    button, span {
      display: inline-flex;
      font-weight: bold;
    }
    svg{
          transform: rotate(-90deg);
    transform-origin: center;
    -ms-transform: rotate(-90deg);
    -ms-transform-origin: center;
    -webkit-transform: rotate(-90deg);
    -webkit-transform-origin: center;
    }

    span {
      margin-left: 5px;
    }

    .selected {
      span {
        color: $primaryColor !important;

      }
    }
  }
}

.canvas-nav-list {
  li {
    // height: 33px;
    padding: 4px 0;

    a, span, button {
      padding: 3px 10px;
      font-size: 12px;
      // height:33px;
    }


  }

  .canvasToggle {
    background-color: $darkBg;
    padding-left: 5px;
    padding-right: 5px;
    border: 1px solid $borderColor;

    a, button {
      padding: 3px 6px !important;
      border-radius: 2px;
      border-right: 1px solid $darkBgSecondary !important;
      margin-right: 0;
    }

    a:last-child, button:last-child {
      border-right: none !important;
    }
  }
}


.aside-nav-list {
  li {
    display: block;
    margin: 0;

    a, span, button {
      padding: 11px 11.5px;
      font-size: 12px;
      border-bottom: 1px solid $borderColor;
    }
  }
}

.nav-right-list {
  li {
    a, span, button {
      background: $borderColor;
    }
  }
}

.nav-left-list, .nav-right-list {
  li {
    padding: 12px 5px;

    a, span, button {
      padding: 3px 5px;
      // border-left: 1px solid $darkBgSecondary;
      background: $darkBgSecondary;
    }

  }
}


.nav-left-list {
  float: left;
}

.nav-right-list {
  float: right;
}


.vertical-list, .vertical-no-border-list {
  li {
    display: block;
    border: none;
    margin: 0;
    padding-bottom: 5px;
    margin-bottom: 5px;

    a, span, button {
      //padding: 15px 17px;
      //padding: 0;
      padding: 5px 0;
    }
  }
}

.vertical-list {
  li {
    border-bottom: 1px solid $borderColor;
  }
}
