@import "../../config";


.tableCanvas {
  overflow-y: scroll;
  height: calc(100% - 20px);
  position: relative;


  table {
    border: 1px solid;
    border-collapse: collapse;

    thead {
      tr {
        background: $darkBg;
        //border-bottom-style: solid;
      }
    }

    th {
      text-transform: uppercase;
      font-size: 10px;
      text-align: center;
    }

    th, td {
      border: 1px solid $borderColor;
      padding: 5px 8px;
      text-align: left;
    }
  }

  .tableComponent {
    display: block;

    h3 {
      margin-bottom: 10px;
      margin-top: 20px;
    }
  }

  .responseBox {
    border-bottom: 2px solid $borderColor;
    padding-bottom: 10px;
  }
}
