@import "../config";

.SelectedDataHeading {
  background: #585858;
  color: $defaultTextColor;
  padding: 3px 0;
  width: 100%;
  font-family: Courier;
  //border: 1px solid $borderColor;


  .itemLabel {
    background: #7d8296;
    padding: 3px 10px;
    /* padding-bottom: 6px; */
    margin-right: 10px;
    text-shadow: 0px 1px #3c3c3c;
    /* color: #888888; */
    font-weight: bold;
  }
}

.SelectedDataList {
  font-family: Courier;
}

.SelectedDataList.mainDetails {
  background: #1e2027;
}

.propertyData {
  padding-left: 5px;
  strong{
    color: $defaultTextColor;
  }
  color: darken($defaultTextColor, 20%);
}

.singleProperty {

  border-bottom: 1px solid darken($darkBg, 3%);
  //padding-top: 3px;
  //padding-bottom: 3px;
  padding: 5px 5px;


}

.singleProperty:nth-child(odd) {
  background: darken($darkBg, 2%);
}

.singleProperty.dark {
  background: #111111;
}

.singleProperty.darkest {
  background: #000000;
}


