  input[name="queryRepeatFrequency"] {
    width: 35px;
    border: transparent;
    border-bottom: 1px solid $borderColor;
    height: auto;
    padding: 0 3px;
    margin: 5px;
  }

  textarea {
    width: calc(100% - 12px);
    //height: calc(100vh - 46px - 24px - 350px);
    //height: 200px;
    position: relative;
    padding: 5px;
    border-radius: 2px;
    background: darken($darkBg, 2%);
